@import "@/assets/global.scss";






























































.scroll-panel {
  width: 100%;
  height: 100%;
  z-index: 10;
  background: #FFD5C2;

  > div {
    width: 100%;
    margin: 0 auto;
    max-width: 366px;
    position: relative;
  }
}

.scroll-list {
  display: flex;
  overflow-x: auto;
}

.scroll-card {
  display: flex;
  flex-direction: column;
  margin: 18px 8px 31px 8px;

  > img {
    border-radius: 12px 12px 0 0;
  }

  > div {
    width: 300px;
    height: 75px;
    background: #FFFFFF;
    border-radius: 0 0 12px 12px;
    position: relative;
    padding: 10px 16px 14px 16px;
  }
}

.item-hint {
  width: 75px;
  height: 25px;
  position: absolute;
  padding: 3px 0;
  top: 7px;
  left: 0;
  background: #F8A580;
  border-radius: 0 12px 12px 0;
  color: #FFFFFF;
}

.item-title {
  padding-left: 69px;
  text-align: left;
  margin-bottom: 16px;
}

.item-time {
  color: #000000;
  mix-blend-mode: normal;
  opacity: 0.4;
}

.dotGroup {
  position: absolute;
  bottom: 13px;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;

  > div {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #C8D0D2;
    margin: 0 4px;
    cursor: pointer;
  }

  > .active {
    background: #FFFFFF;
  }
}
