@import "@/assets/global.scss";




















































.main {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  background: #FFFFFF;

  > div {
    width: 100%;
    max-width: $max-width;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    //justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 16px;
    user-select: none;
  }
}

.link {
  padding: 8px 0;
  > svg {
    fill: #B6C3CA;
  }
}

.router-link-exact-active {
  > div {
    color: #FCA311;
  }

  > svg {
    fill: #FCA311;
  }
}
